"use client";

import React, { useState } from 'react';
import { 
  Filter, 
  Upload, 
  Search as SearchIcon, 
  Plus as PlusIcon, 
  ArrowUpDown as ArrowUpDownIcon, 
  MoreHorizontal as MoreHorizontalIcon, 
  ArrowDownUp as SortIcon
} from "lucide-react";
import { TableCell } from "@/components/monday-table/cells";
import { useTableData } from "@/components/monday-table/hooks/use-table-data";
import { useSelection } from "@/components/monday-table/hooks/use-selection";
import type { Business } from "@/lib/types";
import { Button } from "@/components/ui/button";
import {
  Table,
  TableBody,
  TableCell as UITableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { MoreHorizontal, Search, Plus, ArrowUpDown } from "lucide-react";

const columns = [
  {
    id: "name",
    label: "Business Name",
    type: "text" as const,
    sortable: true,
  },
  {
    id: "owner",
    label: "Owner",
    type: "person" as const,
  },
  {
    id: "phone",
    label: "Phone",
    type: "text" as const,
  },
  {
    id: "type",
    label: "Business Type",
    type: "text" as const,
  },
  {
    id: "status",
    label: "Status",
    type: "status" as const,
    options: [
      { id: "active", label: "Active", color: "#00C875" },
      { id: "inactive", label: "Inactive", color: "#C4C4C4" },
      { id: "lead", label: "Lead", color: "#FDAB3D" },
    ],
  },
  {
    id: "notes",
    label: "Notes",
    type: "text" as const,
  },
];

export function BusinessDataTable({ 
  data, 
  onRowAdd,
  onRowUpdate,
  onRowDelete 
}: { 
  data: Business[], 
  onRowAdd: (business: Partial<Business>) => void,
  onRowUpdate: (id: string, changes: Partial<Business>) => void,
  onRowDelete: (id: string) => void
}) {
  const [isLoading, setIsLoading] = useState(false);
  const {
    handleSort,
    handleFilter,
    sortConfig,
    filters,
  } = useTableData();

  const {
    selectedRows,
    toggleRowSelection,
    selectAllRows,
    isRowSelected,
  } = useSelection();

  return (
    <div className="w-full bg-white rounded-lg shadow-sm border">
      <div className="flex items-center justify-between p-4 border-b">
        <div className="flex items-center gap-4">
          <div className="flex w-[280px] items-center space-x-2 bg-gray-50 rounded-md px-3 py-2">
            <SearchIcon className="h-4 w-4 text-gray-400" />
            <Input
              placeholder="Search businesses..."
              className="h-8 bg-transparent border-0 focus-visible:ring-0 focus-visible:ring-offset-0 text-sm"
              value={filters["global"] || ""}
              onChange={(e) => handleFilter("global", e.target.value)}
            />
          </div>
          <div className="flex items-center gap-2">
            <Button variant="outline" size="sm" className="h-8">
              <Filter className="h-4 w-4 mr-2" />
              Filter
            </Button>
            <Button variant="outline" size="sm" className="h-8">
              <SortIcon className="h-4 w-4 mr-2" />
              Sort
            </Button>
          </div>
        </div>
        <div className="flex items-center gap-2">
          <Button size="sm" variant="outline" className="h-8">
            <Upload className="h-4 w-4 mr-2" />
            Import
          </Button>
          <Button size="sm" className="h-8">
            <PlusIcon className="h-4 w-4 mr-2" />
            Add Business
          </Button>
        </div>
      </div>

      <div className="overflow-x-auto">
        <Table>
          <TableHeader>
            <TableRow className="hover:bg-transparent">
              <TableHead className="w-12 bg-gray-50/50">
                <input
                  type="checkbox"
                  checked={selectedRows.length === data.length}
                  onChange={() => selectAllRows(data.map(r => r.id))}
                  className="h-4 w-4 rounded border-gray-300"
                />
              </TableHead>
              {columns.map((column) => (
                <TableHead key={column.id}>
                  <div className="flex items-center space-x-2">
                    <span>{column.label}</span>
                    {column.sortable && (
                      <ArrowUpDown
                        className="h-4 w-4 cursor-pointer text-muted-foreground"
                        onClick={() => handleSort(column.id)}
                      />
                    )}
                  </div>
                </TableHead>
              ))}
              <TableHead className="w-12"></TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {data.map((row) => (
              <TableRow key={row.id}>
                <UITableCell className="w-12">
                  <input
                    type="checkbox"
                    checked={isRowSelected(row.id)}
                    onChange={() => toggleRowSelection(row.id)}
                    className="h-4 w-4"
                  />
                </UITableCell>
                {columns.map((column) => (
                  <UITableCell key={column.id}>
                    <TableCell
                      column={column}
                      value={row[column.id as keyof Business]}
                      onChange={(value) => onRowUpdate(row.id, { [column.id]: value })}
                    />
                  </UITableCell>
                ))}
                <UITableCell className="w-12">
                  <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                      <Button variant="ghost" className="h-8 w-8 p-0">
                        <MoreHorizontal className="h-4 w-4" />
                      </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="end">
                      <DropdownMenuLabel>Actions</DropdownMenuLabel>
                      <DropdownMenuItem onSelect={() => onRowDelete(row.id)} className="text-destructive">Delete</DropdownMenuItem>
                    </DropdownMenuContent>
                  </DropdownMenu>
                </UITableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>

      <div className="flex items-center justify-between p-4 border-t">
        <p className="text-sm text-gray-600">
          {selectedRows.length} of {data.length} row(s) selected
        </p>
        <div className="flex items-center gap-2">
          <span className="text-sm text-gray-600">
            Page 1 of 1
          </span>
          <div className="flex gap-1">
            <Button variant="outline" size="sm" className="h-8">
              Previous
            </Button>
            <Button variant="outline" size="sm" className="h-8">
              Next
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}