'use client';

export interface Column {
  id: string;
  label: string;
  type: 'text' | 'number' | 'status' | 'person' | 'email' | 'phone' | 'rating' | 'priority' | 'checkbox' | 'timeline';
  options?: Array<{
    id: string;
    label: string;
    color: string;
  }>;
  sortable?: boolean;
}

export interface TableCellProps {
  column: Column;
  value: any;
  onChange: (value: any) => void;
}

export function TableCell({ column, value, onChange }: TableCellProps) {
  switch (column.type) {
    case 'text':
      return (
        <div className="group relative px-4 py-2">
          <input
            type="text"
            value={value || ''}
            onChange={e => onChange(e.target.value)}
            className="w-full bg-transparent border-none focus:outline-none focus:ring-2 focus:ring-primary rounded-sm transition-all duration-200 hover:bg-accent/50 focus:bg-accent/50 px-2 -mx-2"
          />
          <div className="absolute inset-0 pointer-events-none border border-transparent group-hover:border-input rounded opacity-0 group-hover:opacity-100 transition-all duration-200" />
        </div>
      );
    
    case 'status':
      return (
        <div className="group relative px-4 py-2">
          <select
            value={value || ''}
            onChange={e => onChange(e.target.value)}
            className="w-full bg-transparent border-none focus:outline-none focus:ring-2 focus:ring-primary rounded-sm transition-all duration-200 hover:bg-accent/50 focus:bg-accent/50 px-2 -mx-2 cursor-pointer appearance-none"
          >
            {column.options?.map(option => (
              <option key={option.id} value={option.id}>
                {option.label}
              </option>
            ))}
          </select>
          <div className="absolute inset-0 pointer-events-none border border-transparent group-hover:border-input rounded opacity-0 group-hover:opacity-100 transition-all duration-200" />
          <div className="absolute right-4 top-1/2 transform -translate-y-1/2 pointer-events-none">
            <svg
              className="h-4 w-4 text-muted-foreground"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 20 20"
              fill="currentColor"
            >
              <path fillRule="evenodd" d="M10 3a.75.75 0 01.55.24l3.25 3.5a.75.75 0 11-1.1 1.02L10 4.852 7.3 7.76a.75.75 0 01-1.1-1.02l3.25-3.5A.75.75 0 0110 3zm-3.76 9.2a.75.75 0 011.06.04l2.7 2.908 2.7-2.908a.75.75 0 111.1 1.02l-3.25 3.5a.75.75 0 01-1.1 0l-3.25-3.5a.75.75 0 01.04-1.06z" clipRule="evenodd" />
            </svg>
          </div>
        </div>
      );
    
    case 'person':
      return (
        <div className="group relative px-4 py-2">
          <div className="flex items-center space-x-2 rounded-sm transition-all duration-200 hover:bg-accent/50 px-2 -mx-2">
            <div className="w-8 h-8 rounded-full bg-secondary flex items-center justify-center text-secondary-foreground font-medium text-sm overflow-hidden shrink-0">
              {value?.avatarUrl ? (
                <img src={value.avatarUrl} alt={value.name} className="w-full h-full object-cover" />
              ) : (
                <span>{value?.name?.charAt(0).toUpperCase()}</span>
              )}
            </div>
            <span className="truncate">{value?.name}</span>
          </div>
          <div className="absolute inset-0 pointer-events-none border border-transparent group-hover:border-input rounded opacity-0 group-hover:opacity-100 transition-all duration-200" />
        </div>
      );
    
    default:
      return (
        <div className="px-4 py-2">
          {value}
        </div>
      );
  }
}